import { Component, OnInit } from '@angular/core';
import * as L from 'leaflet';

@Component({
  selector: 'app-map',
  templateUrl: './map.component.html',
  styleUrls: ['./map.component.scss']
})
export class MapComponent implements OnInit {

  private map: L.Map;
  circle;
  private centroid: L.LatLngExpression = [20.703921, 72.953697]; //
  markers: L.Marker[] = [];
  marker;
  private initMap(): void {
    this.map = L.map('map', {
      layers: [
        L.tileLayer("https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png", {
          subdomains: ["a", "b", "c"],
          maxZoom: 19
        })
      ],
      center: this.centroid,
      zoom: 15
    });

    // const tiles = L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
    //   maxZoom: 18,
    //   minZoom: 10,
    //   attribution: '&copy; <a href="http://www.openstreetmap.org/copyright">OpenStreetMap</a>'
    // });

    // create 5 random jitteries and add them to map
    // const jittery = Array(5).fill(this.centroid).map( 
    //     x => [x[0] + (Math.random() - .5)/10, x[1] + (Math.random() - .5)/10 ]
    //   ).map(
    //     x => L.marker(x as L.LatLngExpression)
    //   ).forEach(
    //     x => x.addTo(this.map)
    //   );
    
  //   const group = new L.FeatureGroup(this.markers);
  //   group.addTo(this.map);
  //  tiles.addTo(this.map);
   
  }

  constructor() { }

  ngOnInit(): void {
    this.initMap();
    this.circle = L.circle([20.703921, 72.953697], {
      color: 'red',
      fillColor: '#f03',
      fillOpacity: 0.5,
      radius: 500,
      
  }).addTo(this.map);

  
  
//   this.circle.on('mousedown',() =>{
   
//       this.map.on('mousemove', function (e) {
//         // console.log(e.latlng)
//         // this.circle.setLatLng(e);
//       });
//     }
//  ); 
//  this.map.on('mouseup',function(e){
//   this.map.removeEventListener('mousemove');
//  })
    var greenIcon = L.icon({
      iconUrl: 'https://img.icons8.com/fluent/48/000000/marker-storm.png',
      // shadowUrl: 'leaf-shadow.png',
  
      iconSize:     [30, 30], // size of the icon
      // shadowSize:   [50, 64], // size of the shadow
      // iconAnchor:   [22, 94], // point of the icon which will correspond to marker's location
      // shadowAnchor: [4, 62],  // the same for the shadow
      // popupAnchor:  [-3, -76] // point from which the popup should open relative to the iconAnchor
  });
  
  
    this.marker = new L.Marker([20.703921, 72.953697],{icon: greenIcon,draggable: true,}).addTo(this.map).bindPopup("I am a green leaf.").openPopup();
    this.markers.push(this.marker)
    this.init();
  }

  init(){
    var greenIcon = L.icon({
      iconUrl: 'https://img.icons8.com/fluent/48/000000/marker-storm.png',  
      iconSize:     [30, 30], // size of the icon
   
  });
  
    this.map.on('click',<LeafletMouseEvent>(e) =>{
      console.log('click event pass')
    this.map.removeLayer(this.circle);
    this.map.removeLayer(this.marker)
    this.marker = new L.Marker(e.latlng,{icon: greenIcon,draggable: true,}).addTo(this.map).bindPopup("I am a green leaf.").openPopup();
  
    this.markers.push(this.marker)
      this.circle = L.circle(e.latlng, {
        color: 'red',
        fillColor: '#f03',
        fillOpacity: 0.5,
        radius: 500,
        
    }).addTo(this.map);
    })
  }

}
